import { Button, Alert, Typography, Slide, IconButton } from "@mui/material";
import { useState, useEffect } from "react";
import {
  getAllLists,
  logout,
  postRequest,
  checkSession,
} from "../../utils/testApi/testApi";
import {
  colors,
  filterCardsBy,
  groceryContainerTypes,
  groceryListScopes,
  messages,
  URLS,
} from "../../utils/enum";
import "./LandingPage.scss";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Avatar from "@mui/material/Avatar";
import AddIcon from "@mui/icons-material/Add";
import GroceryListCard from "./GroceryListCard/GroceryListCard";
import NewListForm from "./NewListForm/NewListForm";
import { mobileWidth } from "../../utils/enum";
import { useLocation, useNavigate } from "react-router-dom";
import { truncateString } from "../../utils/helper";
// ICONS
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import PublicIcon from "@mui/icons-material/Public";
import KeyIcon from "@mui/icons-material/Key";
import LogoutIcon from "@mui/icons-material/Logout";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import Skeleton from "@mui/material/Skeleton";
import AllInclusiveIcon from "@mui/icons-material/AllInclusive";
// IMGS
import groceryStrip from "../../utils/assets/groceryStrip.jpg";
import todoStrip from "../../utils/assets/todoStrip.jpg";
import shoppingStrip from "../../utils/assets/shoppingStrip.jpg";
import grocery from "../../utils/assets/grocery.jpg";
import shop from "../../utils/assets/shop.jpg";
import todo from "../../utils/assets/todo.jpg";

const meta = {
  meta: {
    charSet: "utf-8",
    name: {
      themeColor: "#ff5733",
    },
    itemProp: {
      name: "theme-color",
      content: "green",
    },
  },
  auto: {
    ograph: true,
  },
};

const LandingPage = ({
  activeList,
  setActiveList,
  user,
  setUser,
  activeContainer,
  setActiveContainer,
}) => {
  // States
  const [filter, setFilter] = useState(filterCardsBy.all);
  const [alertMessage, setAlertMessage] = useState(
    "No lists yet to display. Create your first list "
  );
  const [severity, setSeverity] = useState("info");
  const [newListFormOpen, setNewListFormOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  // Other locals
  const navigate = useNavigate();
  const location = useLocation();

  // Handlers
  const showAlert = (severity, message) => {
    setAlertMessage(message);
    setSeverity(severity);
  };

  const checkItems = async () => {
    const data = {
      containerId: location?.state?.containerId,
      scope: activeList?.scope,
      listId: activeList?.id,
      itemIds: activeList?.groceryListItems
        .filter((item) => item.checked)
        .map((item) => item.id),
    };
    return await postRequest(URLS.checkListItemUri, data);
  };

  const handleDeriveStrip = () => {
    if (activeContainer?.containerType === groceryContainerTypes.grocery) {
      return groceryStrip;
    } else if (activeContainer?.containerType === groceryContainerTypes.todo) {
      return todoStrip;
    } else if (
      activeContainer?.containerType === groceryContainerTypes.whishlist
    ) {
      return shoppingStrip;
    } else {
      return todoStrip;
    }
  };

  const handleLogout = async () => {
    setLoading(true);
    if (activeList?.groceryListItems.length > 0) {
      // Save checked items
      const checkResponse = await checkItems();
      if (checkResponse?.status === 200) {
        // Do nothing, success
      } else if (checkResponse?.status === 403) {
        // Unauthorized, check login session
        navigate("/");
      } else {
        showAlert(
          "error",
          "Apologies. Somehing went wrong on our end. Please refresh the page and try again."
        );
        setLoading(false);
        return;
      }
    }

    // reset states and logout
    const res = await logout();
    if (res.status === 200) {
      // Reset states and navigate to login page
      setUser(null);
      setActiveContainer({ collapsedLists: [] });
      setActiveList({ groceryListItems: [] });
      navigate("/");
    } else {
      showAlert(
        "error",
        "Apologies. Failed log you out. Please refresh the page and try again."
      );
    }
    setLoading(false);
  };

  const getAllList = async () => {
    setLoading(true);
    // Get user info if the user object is null
    let userInfoResponse = { user: user };
    if (!userInfoResponse?.user) {
      userInfoResponse = await checkSession();
      if (userInfoResponse?.status !== 200) {
        navigate("/");
        return;
      } else {
        setUser(userInfoResponse?.user);
      }
    }

    // Fetch lists from container
    const data = {
      userId: userInfoResponse?.user?.id,
      containerId: location?.state?.containerId,
    };
    const res = await getAllLists(data);
    if (res?.status === 200) {
      setActiveContainer(res?.body);
    } else if (res?.status === 401) {
      showAlert("warning", messages.unauthorizedAction);
    } else if (res?.status === 403) {
      console.log(res);
      navigate("/");
    } else if (res?.status === 400) {
      console.log(res);
    } else {
      showAlert("error", messages.genericError);
    }
    setTimeout(() => setLoading(false), 1200);
  };

  const handleDeriveHeadingColor = () => {
    if (activeContainer?.containerType === groceryContainerTypes.grocery) {
      return colors.landingPageColors.low;
    } else if (activeContainer?.containerType === groceryContainerTypes.todo) {
      return colors.todoColors.low;
    } else if (
      activeContainer?.containerType === groceryContainerTypes.whishlist
    ) {
      return colors.shoppingColors.low;
    } else {
      return colors.fallbackColors.low;
    }
  };

  const handleDeriveBodyColor = () => {
    if (activeContainer?.containerType === groceryContainerTypes.grocery) {
      return colors.landingPageColors.bold;
    } else if (activeContainer?.containerType === groceryContainerTypes.todo) {
      return colors.todoColors.bold;
    } else if (
      activeContainer?.containerType === groceryContainerTypes.whishlist
    ) {
      return colors.shoppingColors.bold;
    } else {
      return colors.fallbackColors.bold;
    }
  };

  const handleContainerImg = () => {
    if (activeContainer?.containerType === groceryContainerTypes.grocery) {
      return grocery;
    }
    if (activeContainer?.containerType === groceryContainerTypes.todo) {
      return todo;
    }
    if (activeContainer?.containerType === groceryContainerTypes.whishlist) {
      return shop;
    }
    return grocery; // TODO: error image
  };

  const handlefilterByScope = (lists) => {
    if (filter === filterCardsBy.all) {
      return lists;
    }
    if (filter === filterCardsBy.public) {
      return lists.filter((e) => e.scope === groceryListScopes.public);
    }
    if (filter === filterCardsBy.private) {
      return lists.filter((e) => e.scope === groceryListScopes.private);
    }
    if (filter === filterCardsBy.restricted) {
      return lists.filter((e) => e.scope === groceryListScopes.restricted);
    }
  };

  useEffect(() => {
    // Reset altert mesage
    showAlert("info", "No lists yet to display. Create your first list ");
    // Fetch only if lists are not cached
    if (activeContainer?.id !== location?.state?.containerId) {
      getAllList();
    } else {
      console.debug("Lists are cached. No need to fetch");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <meta name="theme-color" content="white" />
      <Grid
        spacing={1}
        container
        sx={{ maxWidth: mobileWidth, alignItems: "center" }}
      >
        <Grid item>
          <Paper
            elevation={1}
            sx={{
              borderRadius: 0,
              height: "25vh",
              width: "100vmin",
              maxWidth: mobileWidth,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            {loading && (
              <Skeleton
                animation="wave"
                sx={{ borderRadius: 5, maxWidth: `calc(0.9 * ${mobileWidth})` }}
                width={"90vw"}
                height={130}
              />
            )}

            {/* User Info Menu Bar */}
            {!loading && (
              <Stack
                direction="column"
                padding={1}
                sx={{ height: "100%", justifyContent: "space-between" }}
              >
                <Stack
                  direction={"row"}
                  padding={1}
                  spacing={2}
                  sx={{
                    width: "90vw",
                    maxWidth: `calc(0.9 * ${mobileWidth})`,
                    backgroundColor: handleDeriveHeadingColor(),
                    borderRadius: 5,
                    alignItems: "center",
                    justifyContent: "space-between",
                  }}
                >
                  <Typography
                    variant="h5"
                    gutterBottom
                    sx={{
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <IconButton
                      size={"small"}
                      disableRipple
                      onClick={() => navigate(-1)}
                    >
                      <ArrowBackIosIcon />
                    </IconButton>
                    <Avatar
                      sx={{ width: 56, height: 56 }}
                      alt={user?.name}
                      src="[enter path here]"
                    />
                    <span style={{ width: "10px" }} />
                    {user?.name && truncateString(user?.name)}
                  </Typography>

                  <Button
                    disabled={loading}
                    size="small"
                    endIcon={<LogoutIcon />}
                    onClick={() => {
                      handleLogout();
                    }}
                    sx={{
                      color: "black",
                    }}
                  />
                </Stack>

                <Stack
                  direction={"column"}
                  sx={{
                    maxWidth: mobileWidth,
                    height: 40,
                    backgroundImage: `url(${handleDeriveStrip()})`,
                    backgroundSize: "cover",
                  }}
                />
              </Stack>
            )}
          </Paper>
        </Grid>

        {/* Create List Menu */}
        <Grid item>
          {loading && (
            <Skeleton
              animation="wave"
              variant="rectangular"
              height={"35vmin"}
              width={"100vmin"}
              sx={{ maxWidth: mobileWidth }}
            />
          )}
          {!loading && (
            <Paper
              sx={{
                display: "flex",
                width: "100vw",
                maxWidth: mobileWidth,
                borderRadius: 0,
                backgroundColor: handleDeriveBodyColor(),
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Stack direction="column" padding={5} spacing={2}>
                <Button
                  variant={"outlined"}
                  onClick={() => setNewListFormOpen(!newListFormOpen)}
                  sx={{
                    "&:hover": { border: "2px solid white" },
                    borderRadius: 0,
                    border: "2px solid white",
                    color: "white",
                  }}
                >
                  <Typography sx={{ color: "white" }}>
                    Create New List
                  </Typography>
                  <AddIcon fontSize="large" sx={{ color: "white" }} />
                </Button>
              </Stack>
            </Paper>
          )}
        </Grid>

        {/* Filter options below */}
        <Grid item>
          {!loading && (
            <Paper elevation={0}>
              <Stack p spacing={1} width={"100%"} ml direction={"row"}>
                <IconButton
                  onClick={() => setFilter(filterCardsBy.public)}
                  sx={{
                    color:
                      filter === filterCardsBy.public &&
                      handleDeriveBodyColor(),
                    background:
                      filter === filterCardsBy.public &&
                      handleDeriveHeadingColor(),
                    "&:hover": {
                      background: handleDeriveHeadingColor(),
                    },
                  }}
                >
                  <PublicIcon />
                </IconButton>
                <IconButton
                  onClick={() => setFilter(filterCardsBy.all)}
                  sx={{
                    color:
                      filter === filterCardsBy.all && handleDeriveBodyColor(),
                    background:
                      filter === filterCardsBy.all &&
                      handleDeriveHeadingColor(),
                    "&:hover": {
                      background: handleDeriveHeadingColor(),
                    },
                  }}
                >
                  <AllInclusiveIcon />
                </IconButton>
                <IconButton
                  onClick={() => setFilter(filterCardsBy.private)}
                  sx={{
                    color:
                      filter === filterCardsBy.private &&
                      handleDeriveBodyColor(),
                    background:
                      filter === filterCardsBy.private &&
                      handleDeriveHeadingColor(),
                    "&:hover": {
                      background: handleDeriveHeadingColor(),
                    },
                  }}
                >
                  <LockOutlinedIcon />
                </IconButton>
                <IconButton
                  onClick={() => setFilter(filterCardsBy.restricted)}
                  sx={{
                    color:
                      filter === filterCardsBy.restricted &&
                      handleDeriveBodyColor(),
                    background:
                      filter === filterCardsBy.restricted &&
                      handleDeriveHeadingColor(),
                    "&:hover": {
                      background: handleDeriveHeadingColor(),
                    },
                  }}
                >
                  <KeyIcon />
                </IconButton>
              </Stack>
            </Paper>
          )}
        </Grid>

        {/* ListCard below */}
        <Grid item pb>
          {loading && (
            <Stack width={"100vw"} direction={"column"} mt={5}>
              <Skeleton
                animation={"wave"}
                variant="rectangular"
                sx={{ maxWidth: mobileWidth }}
                height={150}
              />
            </Stack>
          )}

          {!loading &&
            activeContainer?.collapsedLists &&
            handlefilterByScope(activeContainer?.collapsedLists).map((e, i) => (
              <GroceryListCard
                username={user?.username}
                activeContainer={activeContainer}
                setActiveContainer={setActiveContainer}
                listInfo={e}
                key={i}
              />
            ))}
        </Grid>

        {activeContainer?.collapsedLists.length === 0 && !loading && (
          <Grid item sx={{ maxWidth: mobileWidth }}>
            <Slide className="alert-slide" in={true} direction="right">
              <Alert severity={severity}>{alertMessage}</Alert>
            </Slide>
            <img
              alt="decorative-background"
              src={handleContainerImg()}
              loading="lazy"
              height={400}
              width={400}
            />
          </Grid>
        )}
      </Grid>

      <NewListForm
        activeContainer={activeContainer}
        setActiveContainer={setActiveContainer}
        user={user}
        open={newListFormOpen}
        setOpen={setNewListFormOpen}
      />
    </>
  );
};

export default LandingPage;
